import navT from '@/components/nav/nav.vue'
import {mapState,mapMutations} from "vuex"
export default {
	components:{navT},
	data() {
		return {
			list:[{
				id:0,
				value:'zfb',
				type:"支付宝支付",
				img:"../../static/img/pay_zfb.png",
				checked:false
			},
			{
				id:1,
				value:'wx',
				type:"微信支付",
				img:"../../static/img/pay_wx.png",
				checked:true
			}
			
			]
		}
	},
	computed:{
		
	},
	methods: {
		...mapMutations(["changePayType"]),
	
		radioChange(evt){
			
			for(var i=0;i<this.list.length;i++){
				if(this.list[i].value == evt.target.value){
					this.current = i
					this.changePayType({
						id:this.list[i].id,
					value:this.list[i].value,
					type:this.list[i].type,
					img:this.list[i].img
					})
					break
				}
			}
			
		}
	},
	onLoad(o) {
		console.log(this.list[0])
			for(var i=0;i<this.list.length;i++){
				this.list[i].checked=false
			}
			this.list[o.id].checked=true
		
		
	}
}
